<template>
    <div class="pos">
        <el-row>
            <el-col :span='7' class="pos-left" id="pos-left">
                <el-tabs>
                    <el-tab-pane label="点餐">
                        <el-table :data = "posDate" border style="width:100%">
                            <el-table-column prop="goodsName" label="商品名称">
                                
                            </el-table-column>
                            <el-table-column prop="count" label="数量" width="100">
                                
                            </el-table-column>
                            <el-table-column prop="price" label="价格"  width="100">
                                
                            </el-table-column>
                            <el-table-column label="设置" fixed="right">
                                <template scope="scope">
                                    <el-button type="text" size="small" @click="delateMeth(scope.row)">删除</el-button>
                                    <el-button type="text" size="small" @click="addList(scope.row)">增加</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                        <div class="tatol">
                            <span>数量：{{tatolCount}} </span>
                            <span>金额：{{tatolMoney}} 元</span>
                        </div>
                        <div class="operation_box">
                            <el-button type="primary">挂单</el-button>
                            <el-button type="danger" @click = "delateAll()">删除</el-button>
                            <el-button type="success" @click = "endBox()">结账</el-button>
                        </div>

                    </el-tab-pane>

                    <el-tab-pane label="挂单">
                        挂单
                    </el-tab-pane>
                    <el-tab-pane label="外卖">
                        外卖
                    </el-tab-pane>                   
                </el-tabs>

            </el-col>
            <!-- 右侧内容 -->
            <el-col :span='17'>
                <div class="com_box">
                    <div class="com_title">商品名称</div>
                    <div class="com_name">
                        <ul>
                            <li v-for="goods in foodBox" @click="addList(goods)">
                                <span>{{goods.goodsName}}</span>
                                <span class="com_price">￥{{goods.price}}元</span>
                            </li>
                        </ul>
                    </div>
                </div>

                <div class="foodlist">
                    <el-tabs>
                        <el-tab-pane label="汉堡">
                            <div>
                                <ul class='cookList'>
                                    <li v-for="food in type0Goods" @click="addList(food)">
                                        <span class="foodImg"><img :src="food.goodsImg" width="100%"></span>
                                        <span class="foodName">{{food.goodsName}}</span>
                                        <span class="foodPrice">￥{{food.price}}</span>
                                    </li>
                                </ul>
                            </div>
                            
                        </el-tab-pane>
                        <el-tab-pane label="饮料">
                             <div>
                                <ul class='cookList'>
                                    <li v-for="food in type1Goods" @click="addList(food)">
                                        <span class="foodImg"><img :src="food.goodsImg" width="100%"></span>
                                        <span class="foodName">{{food.goodsName}}</span>
                                        <span class="foodPrice">￥{{food.price}}</span>
                                    </li>
                                </ul>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="套餐">
                            <div>
                                <ul class='cookList'>
                                    <li v-for="food in type2Goods" @click="addList(food)">
                                        <span class="foodImg"><img :src="food.goodsImg" width="100%"></span>
                                        <span class="foodName">{{food.goodsName}}</span>
                                        <span class="foodPrice">￥{{food.price}}</span>
                                    </li>
                                </ul>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="牛奶">
                            <div>
                                <ul class='cookList'>
                                    <li v-for="food in type3Goods" @click="addList(food)">
                                        <span class="foodImg"><img :src="food.goodsImg" width="100%"></span>
                                        <span class="foodName">{{food.goodsName}}</span>
                                        <span class="foodPrice">￥{{food.price}}</span>
                                    </li>
                                </ul>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    name: 'Pos',
    data(){
        return {
            posDate:[],
            foodBox:[],
            type0Goods:[],
            type1Goods:[],
            type2Goods:[],
            type3Goods:[],
            tatolCount:0,
            tatolMoney:0

        }
    },
    created:function(){
        axios.get('https://www.easy-mock.com/mock/5b8b30dbf032f03c5e71de7f/kuaican/oftenGoods')
        .then(response=>{
            //console.log(response)
            this.foodBox = response.data
        })
        .catch(function(error){
            console.log(error)
        }) 

        axios.get('https://www.easy-mock.com/mock/5b8b30dbf032f03c5e71de7f/kuaican/typeGoods')
        .then(response=>{
            //console.log(response)
            this.type0Goods = response.data[0]
            this.type1Goods = response.data[1]
            this.type2Goods = response.data[2]
            this.type3Goods = response.data[3]
        })
        .catch(function(error){
            console.log(error)
        })        
    },
    methods:{
        addList(goods){//增加商品
            this.tatolCount = 0;
            this.tatolMoney = 0;
            //判断订单是否存在
            let isHave = false;
            for(let i=0; i<this.posDate.length;i++){
                if(this.posDate[i].goodsId == goods.goodsId){
                    isHave = true;
                }
            }


            if(isHave){               
                let arr = this.posDate.filter(a=>a.goodsId == goods.goodsId)
                    arr[0].count++;
            }else{
                let newGoods = {goodsId:goods.goodsId,goodsName:goods.goodsName,price:goods.price,count:1}
                this.posDate.push(newGoods)
            }
            this.getAll()         
        },
        delateMeth(goods){//删除单个商品
            this.posDate = this.posDate.filter(a=>a.goodsId != goods.goodsId);
            this.getAll()
        },
        delateAll(){//删除所有商品
            this.posDate = [];
            this.tatolCount = 0;
            this.tatolMoney = 0;
        },
        getAll(){//得到所有商品数量和钱数
            this.tatolCount = 0;
            this.tatolMoney = 0;
            if(this.posDate){
                this.posDate.forEach((element)=>{
                    this.tatolCount += element.count;
                    this.tatolMoney =  this.tatolMoney+(element.price*element.count)
                })
            }
        },
        endBox(){
            if(this.tatolCount==0){
                this.$confirm('请选择商品', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    // this.$message({
                    //     type: 'success',
                    //     message: '删除成功!'
                    // });
                }).catch(() => {
                    // this.$message({
                    //     type: 'info',
                    //     message: '已取消删除'
                    // });          
                });               
            }else{
                
                this.$confirm('您已经完成付款，期待您的下次光临,', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'success'
                }).then(() => {
                    this.posDate = [];
                    this.tatolCount = 0;
                    this.tatolMoney = 0;
                    // this.$message({
                    //     type: 'success',
                    //     message: '删除成功!'
                    // });
                }).catch(() => {
                    
                    // this.$message({
                    //     type: 'info',
                    //     message: '已取消删除'
                    // });          
                });   
                

            }
        }
    },
    mounted:function(){
        var posHeight = document.body.clientHeight;
        document.getElementById('pos-left').style.height = posHeight +'px'
    }
  
}
</script>


<style scoped>
.pos-left{
    background: #fff;
    border-right:1px solid #000;
}
.operation_box{
    margin-top:10px;
}
.tatol{
    width:100%;
    height:50px;
    line-height: 50px;
    border-bottom: 1px solid #000
}
.tatol span{
    width:50%;
}
.com_title{
    text-align: left;
    height:20px;
    border-bottom: 1px solid #000;
    padding:10px;
}
.com_name ul li{
    list-style:none;
    float: left;
    background:#fff;
    padding:6px;
    margin:10px;
    border-radius: 4px;
    cursor: pointer;
}
.com_name ul li .com_price{
    color:#409eff;
}
.foodlist{
    clear: both;
}




.cookList li{
   list-style: none;
   width:14%;
   border:1px solid #E5E9F2;
   height: auot;
   overflow: hidden;
   background-color:#fff;
   padding: 10px;
   float:left;
   margin: 8px;
   cursor: pointer;
}
.cookList li span{
    display: block;
    float:left;
}
.foodImg{
   width: 40%;
}
.foodName{
   font-size: 14px;
   padding-left: 10px;
   color:brown;
}
.foodPrice{
   font-size: 16px;
   padding-left: 10px;
   padding-top:10px;
}





  .time {
    font-size: 13px;
    color: #999;
  }
  
  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 0;
    float: right;
  }

  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }
  
  .clearfix:after {
      clear: both
  }
</style>



